<template>
    <view class="w-editor-item" :class="[{'read-only': readOnly}]">
        <view class="w-editor-item__top" v-if="!readOnly">
            <view class="">
                <WComponent 
                :type="handleComponentType + 'Header'"
                :value="data.option"
                :read-only="readOnly"
                @input="optionChange"></WComponent>
            </view>
            <view class="w-editor-item__top-bnts">
                <i class="w_editor-icon w_editor-icon-arrowup" v-if="upShow" @click="$emit('upClick')"></i>
                <i class="w_editor-icon w_editor-icon-arrowdown" v-if="downShow" @click="$emit('downClick')"></i>
                <i class="w_editor-icon w_editor-icon-delete" @click="$emit('delClick')"></i>
            </view>
        </view>
        <view class="w-editor-item__content">
            <WComponent
            :type="handleComponentType"
            :value="value"
            :init="init"
            :read-only="readOnly"
            :option="data.option"
            @permit="(e)=>{$emit('permit', e)}"
            @input="inputChange"></WComponent>
        </view>
    </view>
</template>

<script>
    import WComponent from './w_component.vue'
    export default {
        components: {
            WComponent
        },
        props: {
            value: {
              type: [String, Number, Object, Array, Boolean],
              default: undefined,
            },
            data: {
                type: Object,
                default: {}
            },
            upShow: {
                type: Boolean,
                default: false
            },
            downShow: {
                type: Boolean,
                default: false
            },
            readOnly: {
                type: Boolean,
                default: false
            },
            init: {
                type: Boolean,
                default: false
            },
        },
        created() {
            
        },
        computed: {
            handleComponentType() {
                const temp = this.data.type
                const type = `W${temp}`
                return type
            }
        },
        methods: {
            inputChange(e) {
              this.$emit('input', e)
            },
            optionChange(e) {
              this.$emit('optionChange', e)
            },
        }
    }
</script>

<style lang="scss" scoped>
    .w-editor-item{
        width: 100%;
        border: 1px solid #bbb;
        box-sizing: border-box;
        &__top{
            height: 70rpx;
            border-bottom: 1px solid #bbb;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 20rpx;
            &-bnts{
                display: flex;
                &>i{
                    margin-left: 15rpx;
                }
            }
        }
    }
    .read-only{
        border: none;
    }
</style>